<script>
  export let target, disabled = false, marginLeft, marginRight, width = "45%", bg;
  let isAnchor = RegExp("^https?:\/\/").test(target);
  let style = "";
  if(!isAnchor){
    let event = new CustomEvent(target);
    target = function(){
      document.dispatchEvent(event);
    }
  }
  if(!marginLeft && !marginRight) {
    style = "margin-left: 0;margin-right: 0;"
  }else if(!marginRight) {
    style = "margin-right: 0;"
  }else if(!marginLeft) {
    style = "margin-left: 0;"
  }
  if(disabled){
    style = style + "--themeColor: #aaa;";
  }else if(bg){
    style = style + `--themeColor: ${bg};`;
  }
  style = style + `width:${width};`
</script>

<style lang="stylus">
a, button
  margin: 0 10% 0
  box-sizing: border-box
  border solid 1px var(--textColor)
  border-radius: 0.5ch
  padding: 0.5ch
  font-size: 1em
  display flex
  justify-content center
  align-items center
  flex-wrap wrap
  color: #fff
  background-color: var(--themeColor)
  position relative
  transition filter 200ms ease-out 0ms
  filter none
  &:hover
    filter brightness(0.8)
  &:disabled, &.disabled
    &:hover
      filter none
      cursor not-allowed
</style>

{#if isAnchor}
  <a href="{!disabled ? target : 'javascript:void(0);'}" class="{disabled ? 'disabled' : ''}" style="{style}" {disabled}><slot></slot></a>
{:else}
  <button on:click={!disabled ? target : 'javascript:void(0);'} class="{disabled ? 'disabled' : ''}" style="{style}" {disabled}><slot></slot></button>
{/if}